/*Обнуление*/
*,
*:before,
*:after {
  padding: 0;
  margin: 0;
  border: 0;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 10px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}



body {
  font-size: 1.6rem;
  font-family: 'Open Sans', sans-serif;
  background-color: var(--color-white);
  color: var(--color-main-dark);
}


img {
  max-width: 100%;
}

a,
a:focus,
a:active,
a:hover,
a:hover,
a:visited {
  outline: none;
  text-decoration: none;
  color: inherit;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

ul {
  list-style: none;
  padding: 0;
}

.is-outer {
  position: relative !important; // stylelint-disable-line declaration-no-important
  left: -9999px !important; // stylelint-disable-line declaration-no-important
}

.site-container {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.is-hidden {
  display: none !important; // stylelint-disable-line declaration-no-important
}

.for-mobile {
  display: none;

  @include mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important; // stylelint-disable-line declaration-no-important
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 10px;
  max-width: 1400px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}


main {
  display: flex;
  flex: 1 1 100%;

  img {
    width: 100%;
    object-fit: cover;

  }
}
